<template lang="pug">
    .new__group-content
        .programmes__title Группа
        form(@submit='saveGroup()').new__group
            .new__group-points
                .form__item-group-img
                    img(src='../../../assets/images/icons/panda1.svg').img
                .form__item-group
                    label.label__name Тип группы
                    select(v-model="group.type").new__group-type
                        option(value='') Выбрать
                        option(value='Индивидуально') Индивидуально
                        option(value='Минигруппа') Минигруппа
                .form__item-group
                    label.label__name Департамент
                    select(@change="onChangeDepartment()" v-model="group.department.id").new__group-department
                        option(value=0) Выбрать
                        option(:value='item.id' v-for='item in departments') {{item.name}}
                .form__item-group
                    label.label__name Предмет
                    select(v-model="group.subject.id").new__group-subject
                        option(value=0) Выбрать
                        option(:value='item.id' v-for='item in filteredSubjects') {{item.name}}
            .new__group-points
                .form__item-group
                    label.label__name(for='class') Класс
                    select(v-model="group.groupClass").new__group-class
                        option(value=0) Выбрать
                        option(:value='item' v-for='item in 12') {{item}}
                .form__item-group
                    label.label__name Тип учебной программы
                    select(v-model="group.eduProgrammeType").new__group-programme-type
                        option(value='') Выбрать
                        option(:value='item' v-for='item in programmeTypes') {{item}}
                .form__item-group
                    label.label__name Преподаватель
                    select(@change='onChangeTeacher()' v-model="group.teacher.id").new__group-teacher
                        option(value=0) Выбрать
                        option(:value='teacher.id' v-for='teacher in filteredTeachers') {{teacher.lastName}} {{teacher.firstName}}
                .form__item-group
                    label.label__name Язык обучения
                    select(v-model="group.language").new__group-language
                        option(value='') Выбрать
                        option(value='Казахский') Казахский
                        option(value='Русский') Русский
            
            
            .new__group-points
                .new__group-schedule
                    .new__group-schedule__title Расписание
                    ul.new__group-schedule__list
                        li(v-for='item, idx in days').new__group-schedule__item {{item}}
                            new-group-schedule(
                                :selectedTime='selectedTime'
                                :day='idx+1'
                                :teacherSchedule='teacherSchedule'
                                :index='idx+1'
                                @scheduleTimeSelect='scheduleTimeSelect'
                            )
            .new__group-buttons
                button(type='submit').save__button-new-group  Сохранить
                button(@click.prevent='resetGroup').reset__button-new-group  Отмена
            
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        newGroupSchedule: () => import("../../admin/groups/new-group-schedule")
    },
    props: {
        departments: Array,
        subjects: Array,
        teachers: Array
    },
    data() {
        return {
            selectedTime: 0,
            group: {
                language: '',
                groupClass: 0,
                groupSchool: '',
                department: {
                    id: 0
                },
                subject: {
                    id: 0
                },
                teacher: {
                    id: 0
                },
                type: '',
                eduProgrammeType: '',
                schedule: []
            },
            days: [
                'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'
            ],
            programmeTypes: [
                'основная',
                'ЕНТ',
                'подготовка в спец.школу',
                'подготовка к олимпиаде',
            ]
        }
    },
    computed: {
        ...mapState ("teacherSchedule", {
            teacherSchedule: state => state.teacherSchedule}),
        filteredTeachers: function() {
            return this.teachers
            //filter by department
            .filter(ele=> ele.department.id==this.group.department.id || this.group.department.id == 0)
        },
        filteredSubjects: function() {
            return this.subjects
            //filter by department
            .filter(ele=> ele.department.id==this.group.department.id || this.group.department.id  == 0)
        }
    },
    methods: {
        ...mapActions("teacherSchedule", ["fetchTeacherSchedule"]),
        ...mapActions("groups", ["addGroup"]),
        ...mapActions("error", ["fetchError"]),
        onChangeTeacher(){
            this.fetchTeacherSchedule(this.group.teacher.id)
            this.selectedTime=0
        },
        saveGroup() {
            if(this.group.language=='' ||
                this.group.groupClass==0 ||
                this.group.groupSchool=='' ||
                this.group.department.id==0 ||
                this.group.subject.id==0 ||
                this.group.type =='' ||
                this.group.eduProgrammeType==''
            ){
                this.fetchError('Не все поля заполнены!')
            }
            else{
                this.addGroup(this.group)
                this.$emit('eventNewGroup')
            }
        },
        resetGroup() {
            this.$emit('eventNewGroup')
        },
        onChangeDepartment() {
            this.group.subject.id = 0
            this.group.teacher.id = 0
            this.group.language = ''
            this.group.id = 0
        },
        scheduleTimeSelect(data){
            if (data.scheduleTime != 0){
                this.group.schedule.push(data)
            }
            else if (
                this.group.schedule.filter(element => 
                element.scheduleItem.scheduleDay == data.scheduleDay).length!=0){
                    this.group.schedule.forEach((element, idx) => {
                        if (element.scheduleItem.scheduleDay == data.scheduleDay && data.scheduleTime == 0){
                            this.group.schedule.splice(idx,1)
                        }
                    });
                }
        }

    }
}
</script>
